.nav {
    width: 300px;
    display: flex;
    justify-content: space-between;

}

.text {
    color: #777;
    text-decoration: none;
    font-family: "Montserrat", sans-serif;
    line-height: 40px;
    letter-spacing: 0.4px;
    font-size: 14px;
    cursor: pointer;
}

a:hover, .active{
    text-decoration: line-through;
    color: #afafaf;
}
